import {
  inputRequiredValidateObj,
  nonSpecialCharValidateObj,
  selectRequiredValidateObj,
} from '@/common/validator/common';

let getReceiptNoFormRules = (scope) => {
  return {
    receiptNo: [nonSpecialCharValidateObj, inputRequiredValidateObj, checkReceiptNoValidateObj(scope)],
  };
};

let getTransactionFormRules = () => {
  return {
    initiationTime: [selectRequiredValidateObj],
    debitParty: [nonSpecialCharValidateObj, inputRequiredValidateObj, selectRequiredValidateObj],
    creditParty: [nonSpecialCharValidateObj, inputRequiredValidateObj, selectRequiredValidateObj],
    initCreditParty: [{ require: false }],
    initDebitParty: [{ require: false }],
  };
};

let getReverseAmountRules = (detailInfo) => {
  let reverseAmount = (rule, value, callback) => {
    if (!value) {
      callback();
      return;
    }
    let maxAmount = detailInfo.totalStrAmount;
    let reverseMode = detailInfo.reverseMode;
    let newValue = value.replaceAll(',', '');
    // 部分回滚
    if (reverseMode === 'P') {
      if (Number(newValue) >= Number(maxAmount)) {
        return callback(new Error('Please enter an amount less than【' + maxAmount + '】'));
      } else {
        callback();
      }
    }
    // 部分或全部回滚
    if (reverseMode === 'B') {
      if (Number(newValue) > Number(maxAmount)) {
        return callback(new Error('Please enter a maximum amount of【' + maxAmount + '】'));
      } else {
        return callback();
      }
    }
  };
  return {
    reverseAmount: [{ validator: reverseAmount, trigger: 'blur' }],
  };
};

let checkReceiptNoValidateObj = (scope) => {
  return {
    trigger: 'blur',
    validator: (rule, value, callback) => {
      if (scope.receiptNoExist) {
        return callback();
      }
      return callback('The Receipt Number Does Not Exist.');
    },
  };
};

export { getReverseAmountRules, getReceiptNoFormRules, getTransactionFormRules };
