  },
  computed: {
    isShowReverse() {
      if (this.routeParams.type === 'view') {
        return false;
      }
      return (
        this.transactionDetail.reverseMode &&
        (this.transactionDetail.status === 'Completed' || this.transactionDetail.status === 'Paid')
      );
    },
    isShowProcess() {
      if (this.routeParams.type === 'view') {
        return false;
      }
      return this.transactionDetail.status === 'Authorized' || this.transactionDetail.status === 'Pending Authorized';
    },
    basicInfo: {
      get() {
        let disputedTransaction = [
          {
            title: this.$t('IDC.reviewTransaction.label.disputedTransaction'),
            prop: 'disputedTransaction',
            content: '-',
            co: 8,
          },
        ];
        let additionalInfo = [
          {
            title: this.$t('IDC.reviewTransaction.label.accumulatorUpdate'),
            prop: 'accumulatorUpdate',
            content: '-',
            co: 8,
            showDetail: true,
          },
          {
            title: this.$t('IDC.reviewTransaction.label.detailsOfChargeRating'),
            prop: 'detailsOfChargeRating',
            content: '-',
            co: 8,
            showDetail: true,
          },
          {
            title: this.$t('IDC.reviewTransaction.label.disputedTransaction'),
            prop: 'disputedTransaction',
            content: '-',
            co: 8,
          },
        ];
        if (this.transactionDetail.status === 'Authorized') {
          return this.tempBasicInfo.concat(additionalInfo);
        } else {
          return this.tempBasicInfo.concat(disputedTransaction);
        }
      },
      set(val) {
        return val;
      },
    },
    currencySymbol() {
      let currencyObj = AmountUnit.getCurrency(this.currency) || {};
      let currencySymbol = currencyObj.symbol || '';
      return currencySymbol ? `(${currencySymbol})` : currencySymbol;
    },
    statusStyle() {
      return (status) => {
        switch (status) {
          case 'Initiated':
            return {
              'background-color': `${this.GlobalStyleVariables.statusNoticeBackgroundColor}`,
              color: `${this.GlobalStyleVariables.statusNoticeColor}`,
            };
          case 'Completed':
            return {
              'background-color': `${this.GlobalStyleVariables.statusSuccessBackgroundColor}`,
              color: `${this.GlobalStyleVariables.statusSuccessColor}`,
            };
          case 'Pending':
          case 'Authorized':
          case 'Pending Authorized':
          case 'Refund':
            return {
              'background-color': `${this.GlobalStyleVariables.statusSecondaryWarningBackgroundColor}`,
              color: `${this.GlobalStyleVariables.statusColorSAecondaryWarning}`,
            };
          case 'Cancelled':
          case 'Declined':
          case 'Expired':
            return {
              'background-color': `${this.GlobalStyleVariables.statusDarkBackgroundColor}`,
              color: `${this.GlobalStyleVariables.statusDark}`,
            };
          default:
            return {
              'background-color': `${this.GlobalStyleVariables.statusNoticeBackgroundColor}`,
              color: `${this.GlobalStyleVariables.statusNoticeColor}`,
            };
        }
      };
    },
    linkedStatusStyle() {
      return (status) => {
        switch (status) {
          case 'COMPLETED':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case 'PENDING':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorSAecondaryWarning}` };
          case 'FULL_REFUND':
            return { 'background-color': `${this.GlobalStyleVariables.statusErrorColor}` };
          default:
            return { 'background-color': `${this.GlobalStyleVariables.statusNoticeColor}` };
        }
      };
    },
  },
  mounted() {
    this.isSearch = true;
    let params = this.$route.query.params;
    this.routeParams = (params && JSON.parse(decodeURIComponent(params))) || '';
    this.getDetail();
  },
  activated() {},
  methods: {
    async getDetail() {
      const { basicInfo } = this;
      let params = {
        tradeOrderId: this.routeParams.tradeOrderId,
        identityId: this.routeParams.identityId,
        identityType: this.routeParams.identityType,
      };
      let res = await queryDetail(params);
      if (res.header && res.header.success) {
        let isShowForwardDetails = this.isShowForwardDetails(this.tradeOrderId, res && res.basicDetail);
        this.transactionDetailsInfo =
          (res &&
            res.basicDetail.filter((item) => {
              return isShowForwardDetails ? item.isReversed === 0 : item.isReversed === 1;
            })) ||
          [];
        this.linkedTransactionTableData =
          (res &&
            res.basicDetail.filter((item) => {
              return isShowForwardDetails ? item.isReversed === 1 : item.isReversed === 0;
            })) ||
          [];
        this.transactionDetail = this.transactionDetailsInfo[0];
        this.transactionDetail.identityId = this.routeParams.identityId;
        this.transactionDetail.identityType = this.routeParams.identityType;

        // 新增过滤条件
        let filterEntryList =
          res.entryList.filter((item) => {
            return this.transactionDetail.isReversed === item.isReversed;
          }) || [];
        filterEntryList = filterEntryList.map((item) => {
          if (item.transactionAmount) {
            item.transactionAmount = AmountUnit.format(item.transactionAmount, item.transactionCurrency);
          }
          if (item.availableBalance) {
            item.availableBalance = AmountUnit.format(item.availableBalance, item.transactionCurrency);
          }
          if (item.runningBalance) {
            item.runningBalance = AmountUnit.format(item.runningBalance, item.transactionCurrency);
          }
          return item;
        });

        this.referenceDatas = this.getReferenceTableData(this.transactionDetailsInfo[0].referenceData);
        this.accountEntries = filterEntryList;
        this.orderInteractionLogList = res.orderInteractLogList || [];
        this.outboundMessageLogList = res.outboundMsgLogList || [];

        let detailData = this.transactionDetail;
        this.basicInfo = basicInfo.map((basic) => {
          const key = basic.prop;
          const propValue = detailData[basic.prop];
          if (key === 'createTime' || key === 'lastUpdateTime') {
            basic.content = dayjs(propValue).format('DD/MM/YYYY HH:mm:ss');
          } else {
            if (key === 'totalStrAmount') {
              this.currency = detailData.totalCurrency;
              basic.title = this.$t('IDC.reviewTransaction.label.amount') + this.currencySymbol;
            }
            basic.content = propValue ? propValue : '-';
          }
          return basic;
        });
      }
    },
    // 显示正向的详情
    isShowForwardDetails(requestParam, basicDetails) {
      if (basicDetails == null || basicDetails.length === 0) {
        return true;
      }
      let isTradeOrderId = requestParam === basicDetails[0].tradeOrderId;
      if (isTradeOrderId) {
        return true;
      }
      for (let i = 0; i < basicDetails.length; i++) {
        let detail = basicDetails[i];
        if (requestParam === detail.paymentOrderId) {
          return detail.isReversed === 0;
        }
      }
      return true;
    },
    getReferenceTableData(referenceData = {}) {
      let tableDataArr = [];
      if (JSON.stringify(referenceData) !== '{}') {
        for (let key in referenceData) {
          tableDataArr.push({ referenceKey: key, referenceValue: referenceData[key] });
        }
      }
      return tableDataArr;
    },
    accumulatorGetTableData() {},
    ratingGetTableData() {},
    showDetailDialog(item) {
      if (item.prop === 'accumulatorUpdate') {
        this.accumulatorVisible = true;
      } else {
        this.ratingVisible = true;
      }
    },
    handleAccumulatorClose() {
      this.accumulatorVisible = false;
    },
    handleRatingClose() {
      this.ratingVisible = false;
    },
    showOrganizationDetail(row) {
      let params = {
        organizationId: row.identityId,
        propShortCode: '',
        identityId: row.identityId,
        identityType: row.identityType,
      };
      let path = '/mainPage/identifierCenter/organizationInfo/organization/detail';
      this.openNewWindow({
        path: path,
        query: { data: encodeURIComponent(JSON.stringify(params)) },
      });
    },
    showLinkedTransactionDetail(row) {
      let params = {
        tradeOrderId: row.tradeOrderId,
        identityId: this.routeParams.identityId,
        identityType: this.routeParams.identityType,
        status: row.status,
        type: 'detail',
        from: 'transaction',
      };
      let path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.openNewWindow({
        path: path,
        query: { params: encodeURIComponent(JSON.stringify(params)) },
      });
    },
    showIdentityType(val) {
      switch (val) {
        case '1000':
          return 'customer';
        case '7000':
          return 'group';
        case '5000':
          return 'organization';
        case '8000':
          return 'sp';
        default:
          return 'all';
      }
    },
  },
};
</script>

