<template>
  <div class="page detail-flex-box-left">
    <el-form ref="reverseRef" :model="formData" :rules="formRules" size="small" label-position="top">
      <div class="content-title">
        {{ title }}
      </div>
      <el-divider></el-divider>
      <div>
        <div class="subtitle">{{ $t('IDC.reviewTransaction.label.initiateScenario') }}</div>
        <div class="detail-content">{{ initiateScenarioDesc }}</div>
      </div>
      <el-form-item
        v-if="isProcessTask"
        :label="$t('IDC.reviewTransaction.label.action')"
        prop="action"
        style="padding-bottom: 13px"
      >
        <el-radio-group v-model="formData.action" size="small" style="margin-top: -8px">
          <el-radio v-for="(item, index) in labelActions" :key="index" :label="item.toLowerCase()">
            {{ item }}
          </el-radio>
        </el-radio-group>
      </el-form-item>
      <div v-if="canReverse">
        <el-form-item
          :label="$t('IDC.reviewTransaction.label.reverseAmount') + this.currencySymbol"
          prop="reverseAmount"
          class="can-reverse-form-item"
        >
          <el-input
            v-model="formData.reverseAmount"
            :placeholder="'0.00'"
            @focus="unInputMoney($event)"
            @blur="inputMoney($event)"
          />
        </el-form-item>
        <div class="amount-box">
          <span>{{ $t('IDC.reviewTransaction.label.transactionAmount') }}: </span>
          <span>{{ this.symbol + transactionDetail.totalStrAmount }}</span>
        </div>
      </div>
      <el-form-item :label="$t('IDC.reviewTransaction.label.remark')" prop="reason">
        <el-select v-model="formData.reason" :placeholder="$t('common.placeholder.select')">
          <el-option v-for="item in reasonOptions" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="remark">
        <el-input type="textarea" :rows="5" v-model="formData.remark" :placeholder="$t('common.placeholder.enter')" />
      </el-form-item>
    </el-form>
    <div class="bottom-box">
      <el-button id="cancel" class="cancel-button" size="small" @click="cancel()">
        {{ $t('common.button.cancel') }}
      </el-button>
      <el-button
        id="Submit"
        class="next-button"
        size="small"
        type="primary"
        :loading="submitLoading"
        @click="submitData"
        v-preventReClick
      >
        {{ $t('common.button.submit') }}
      </el-button>
    </div>
  </div>
</template>

<script>
import { reverseTransaction } from '@/views/identifier-center/api/api-transaction.js';
import { getReverseAmountRules } from '@/views/identifier-center/validator/reverse-transaction.js';
import { selectRequiredValidateObj, inputRequiredValidateObj, len512StrValidateObj } from '@/common/validator/common';
import { batchApprovalTransaction, confirmTransaction } from '@/views/identifier-center/api/api-transaction.js';
import AmountUnit from '@/views/business-center/pages/settlement/utils/amount-utils.js';

export default {
  name: 'ReverseContent',
  components: {},
  props: {
    transactionDetail: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      canReverse: false,
      formData: {
        reverseAmount: '',
        action: '',
        reason: '1',
        remark: '',
      },
      reasonOptions: [{ value: '1', label: 'Input Manually...' }],
      formRules: {
        action: [selectRequiredValidateObj],
        reason: [selectRequiredValidateObj],
        remark: [inputRequiredValidateObj, len512StrValidateObj],
      },
      submitLoading: false,
      currency: '',
    };
  },
  computed: {
    isProcessTask() {
      return this.transactionDetail.status === 'Pending Authorized' || this.transactionDetail.status === 'Authorized';
    },
    labelActions() {
      if (this.transactionDetail.status === 'Pending Authorized') {
        return ['Approve', 'Reject'];
      } else if (this.transactionDetail.status === 'Authorized') {
        return ['Complete', 'Cancel'];
      } else {
        return [];
      }
    },
    title() {
      if (this.isProcessTask) {
        return this.$t('IDC.reviewTransaction.processTitle');
      } else {
        return this.$t('IDC.reviewTransaction.label.reverse');
      }
    },
    initiateScenarioDesc() {
      if (this.isProcessTask) {
        return this.$t('IDC.reviewTransaction.label.initiateScenarioProcessDesc');
      } else {
        return this.$t('IDC.reviewTransaction.label.initiateScenarioDesc');
      }
    },
    currencySymbol() {
      let currencyObj = AmountUnit.getCurrency(this.currency) || {};
      let currencySymbol = currencyObj.symbol || '';
      return currencySymbol ? `(${currencySymbol})` : currencySymbol;
    },
    symbol() {
      let currencyObj = AmountUnit.getCurrency(this.currency) || {};
      let currencySymbol = currencyObj.symbol || '';
      return currencySymbol;
    },
  },
  watch: {
    transactionDetail: {
      handler(value) {
        if (value) {
          this.currency = value.totalCurrency;
          this.canReverse = value.reverseMode && value.reverseMode != 'F';
          // 如果是全额回滚，不显示输入框，设置默认reverseAmount（提交回滚使用）
          if (!this.canReverse) {
            this.formData.reverseAmount = value.totalStrAmount;
          }
          this.formRules.reverseAmount = getReverseAmountRules(value).reverseAmount;
        }
      },
      immediate: true,
      deep: true,
    },
  },
  created() {},
  activated() {},
  mounted() {},
  methods: {
    cancel() {
      this.$confirm('Are you sure to reset all data you have entered/selected in this page?', '', {
        confirmButtonText: this.$t('common.button.confirm'),
        cancelButtonText: this.$t('common.button.cancel'),
        closeOnClickModal: false,
        type: 'warning',
      }).then(() => {
        this.$refs['reverseRef'].resetFields();
      });
    },
    async submitData() {
      this.submitLoading = true;
      this.$refs['reverseRef'].validate((valid) => {
        if (valid) {
          if (this.isProcessTask) {
            this.handleProcessTask();
          } else {
            this.handleReverse();
          }
        }
      });
    },

    async handleReverse() {
      const reverseAmount = this.formData.reverseAmount.replaceAll(',', '');
      let params = {
        tradeOrderId: this.transactionDetail.tradeOrderId,
        reverseAmount: {
          amount: reverseAmount,
          currencyCode: this.transactionDetail.totalCurrency,
          unitType: 'BASIC',
        },
        remark: this.formData.remark,
      };
      let res = await reverseTransaction(params);
      this.submitLoading = false;
      if (res.header && res.header.success) {
        this.$modal.msgSuccess('Success to submit.');
        setTimeout(() => {
          this.goTransactionDetail();
        }, 1000);
      }
    },

    async handleProcessTask() {
      let action = this.formData.action;
      let params = {};
      let res;
      if (action === 'approve' || action === 'reject') {
        params = {
          actionType: action,
          remark: this.formData.remark,
          tradeOrderIdList: [this.transactionDetail.tradeOrderId],
        };
        res = await batchApprovalTransaction(params);
      } else {
        params = {
          operationType: action === 'cancel' ? 'CANCEL' : 'CONFIRM',
          tradeOrderIdList: [this.transactionDetail.tradeOrderId],
          remark: this.formData.remark,
          serviceCode: '',
          reasonType: '',
        };
        res = await confirmTransaction(params);
      }
      this.submitLoading = false;
      if (res.header && res.header.success) {
        let msg = '';
        switch (this.formData.action) {
          case 'approve':
            msg = this.$t('IDC.reviewTransaction.approveTip');
            break;
          case 'reject':
            msg = this.$t('IDC.reviewTransaction.rejectTip');
            break;
          case 'complete':
            msg = this.$t('IDC.reviewTransaction.completeTip');
            break;
          case 'cancel':
            msg = this.$t('IDC.reviewTransaction.cancelTip');
            break;
          default:
            msg = '';
            break;
        }
        this.$modal.msgSuccess(msg);
        setTimeout(() => {
          this.refreshCurrentPage();
        }, 1000);
      }
    },
    goTransactionDetail() {
      let params = {
        tradeOrderId: this.transactionDetail.tradeOrderId,
        identityId: this.transactionDetail.identityId,
        identityType: this.transactionDetail.identityType,
        type: 'view',
      };
      let path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.$router.replace({
        path: path,
        query: { params: encodeURIComponent(JSON.stringify(params)) },
      });
      this.refreshCurrentPage();
    },

    // 失去焦点金额格式化
    inputMoney(el) {
      let temp = Number(el.target.value) || null;
      this.formData.reverseAmount = this.priceFormat(temp);
    },
    // 获得焦点金额去掉格式
    unInputMoney(el) {
      if (el.target.value) {
        this.formData.reverseAmount = this.delcommafy(el.target.value);
      } else {
        this.formData.reverseAmount = null;
      }
    },
    priceFormat(num, n) {
      n = n || 2;
      let symbol = ',';
      if (num === null) return num;
      if (typeof num !== 'number') throw new TypeError('The num parameter should be of the number type.');
      if (n < 0) throw new Error('Parameter n should not be less than 0.');
      let hasDot = parseInt(num) !== num; // 这里检测num是否为小数，true表示小数
      let m = n !== undefined && n != null ? n : 1;
      num = m === 0 ? num.toFixed(m) + '.' : hasDot ? (n ? num.toFixed(n) : num) : num.toFixed(m);
      symbol = symbol || ',';
      num = num.toString().replace(/(\d)(?=(\d{3})+\.)/g, function (match, p1) {
        return p1 + symbol;
      });
      if (n === 0 || (!hasDot && !n)) {
        // 如果n为0或者传入的num是整数并且没有指定整数的保留位数，则去掉前面操作中的小数位
        num = num.substring(0, num.indexOf('.'));
      }
      return num;
    },
    // 去除千分位中的‘，’
    delcommafy(num) {
      if (!num) return num;
      num = num.toString();
      num = num.replace(/,/gi, '');
      if (num.indexOf('.00') > 0) num = parseInt(num);
      return num;
    },
  },
};
</script>

<style scoped lang="scss">
.page {
  .can-reverse-form-item {
    margin-bottom: 4px !important;
  }
  .amount-box {
    background-color: $--background_color--blue;
    color: $--status_color--notice;
    font-size: 12px;
    padding: 8px 0 8px 12px;
    margin-bottom: 20px;
  }

  .content-title {
    color: $--text_color--emphasis;
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 10px;
  }
  .subtitle {
    color: $--text_color--secondary;
    font-weight: 400;
    font-size: 14px;
    margin-top: 10px;
  }

  .detail-content {
    margin-top: 8px;
    margin-bottom: 20px;
    color: $--text_color--strong;
    font-weight: 400;
    font-size: 14px;
  }

  .bottom-box {
    padding-bottom: 0;
    display: flex;
    justify-content: flex-end;
  }
  /deep/ .el-form-item__error {
    position: relative;
  }
}
</style>
